.navbar {
  position: sticky;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 100;
  background-color: white;
}
.unm {
  cursor: pointer;
  position: relative;
  height: 100%;
}
.unmc {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 0.5vw;
  position: relative;
}
.unmc_inner {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.unmc_inner > img {
  height: 2.5vw;
}
.unmc_inner > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
}
.unm_list_container_click {
  display: block;
  position: absolute;
  background: #ffffff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  /* padding: 1rem; */
  min-width: 35rem;
  max-width: 35rem;
  max-height: 500px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: -4px 9px 21px -9px rgba(0, 0, 0, 0.75);
  overflow-x: hidden;
  overflow-y: auto;
  scroll-behavior: smooth;
  z-index: 1300;
}
.unmlc_triangle_click {
  display: block;
  position: absolute;
  bottom: 0;
  border-bottom: 10px solid #6c6c6c;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  z-index: 1300;
}

.unm_list_container {
  display: none;
  position: absolute;
  background: #ffffff;
  border: 0.5px solid #cacaca;
  border-radius: 6px;
  /* padding: 1rem; */
  min-width: 35rem;
  max-width: 35rem;
  max-height: 500px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: -4px 9px 21px -9px rgba(0, 0, 0, 0.75);
  overflow-x: hidden;
  overflow-y: auto;
  scroll-behavior: smooth;
}
.unmlc_triangle {
  display: none;
  position: absolute;
  bottom: 0;
  border-bottom: 10px solid #6c6c6c;
  border-right: 10px solid transparent;
  border-left: 10px solid transparent;
  z-index: 1300;
}
.unm:hover > .unm_list_container,
.unm:hover .unmlc_triangle {
  display: block;
}
.unm:hover > .unm_list_container {
  z-index: 1200;
}
.unmlc_card {
  padding: 0.5rem 2rem;
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  width: 100%;
  cursor: pointer;
}
.unmlc_card:hover {
  background: rgba(75, 26, 133, 0.1);
}
.unmlc_card_imgg {
  height: 1.4rem;
}
.unmlc_card_img {
  height: 1.6rem;
}
.unmlc_card > h6 {
  color: var(--t-head-color);
  font-size: var(--font-16);
  font-weight: var(--font-6);
  line-height: var(--line-18);
  margin: 0;
}
